h1 {
  font-family: Roboto, sans-serif;
  font-weight: 400;
}

@font-face {
  font-family: Roboto, Arial, Helvetica, sans-serif;
  src: url("Roboto-Regular-webfont.55e67705.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

html, body, div, span, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, code, del, dfn, em, img, q, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td {
  font-weight: inherit;
  font-style: inherit;
  box-sizing: border-box;
  border: 0;
  margin: 0;
  padding: 0;
  font-family: inherit;
  font-size: 100%;
}

em {
  font-style: italic;
}

strong {
  font-weight: bold;
}

:root {
  scroll-behavior: smooth;
  font-family: Inter, sans-serif;
}

html, body {
  background: #f7f7f7;
  height: 100%;
  margin: 0;
  padding: 0;
}

a {
  color: #000;
  text-decoration: none;
}

.no-scroll {
  max-height: 100vh;
  overflow: hidden;
}

.header {
  justify-content: space-between;
  align-items: center;
  height: 48px;
  margin-bottom: 90px;
  padding: 40px 123px 0;
  display: flex;
}

.header__logo {
  width: 161px;
  height: 26.2px;
}

.header__list, .header__inner-list {
  align-items: center;
  gap: 47px;
  display: flex;
  position: relative;
}

.header__item {
  list-style: none;
}

.header__item:first-of-type {
  font-size: 14px;
  font-weight: 400;
  line-height: 20px;
}

.header__item:first-of-type .header__link {
  color: #4f4f4f;
}

.header__item:nth-of-type(2) {
  color: #333;
  font-size: 14px;
  font-weight: 400;
  line-height: 20px;
}

.header__en {
  color: #333;
}

.header__ua {
  color: #bdbdbd;
}

.header__item:nth-of-type(3) {
  background-color: #333;
  justify-content: center;
  align-items: center;
  width: 102px;
  height: 48px;
  display: flex;
}

.header__item:nth-of-type(3) .header__link {
  color: #fff;
  font-size: 12px;
  font-weight: 700;
  line-height: 14px;
}

.header__burger-menu {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 4px;
  width: 24px;
  height: 16px;
  display: none;
}

.header__burger-menu span {
  background-color: #292929;
  border-radius: 2px;
  height: 2.5px;
  transition: transform .3s;
}

.header__burger-menu--active span:nth-of-type(2) {
  display: none;
}

.header__burger-menu--active span:first-of-type {
  transform-origin: center;
  transform: rotate(-45deg)translateY(4.5px);
}

.header__burger-menu--active span:last-of-type {
  transform-origin: center;
  transform: rotate(45deg)translateY(-4.5px);
}

.header__burger-menu-part {
  background-color: #fff;
  width: 25px;
  height: 2px;
  display: block;
}

@media screen and (width >= 768px) and (width <= 1439px) {
  .header {
    margin-bottom: 80px;
    padding: 40px 34px 0;
  }

  .header__burger-menu {
    display: flex;
  }

  .speaker {
    justify-content: center;
    align-items: end;
    margin-bottom: 140px;
    padding: 0 34px;
    position: relative;
    overflow: hidden;
  }

  .speaker__info {
    width: 40%;
    max-width: none;
  }

  .speaker__title {
    font-size: 48px;
    line-height: 62px;
  }

  .speaker__text {
    max-width: 80%;
  }

  .speaker__visual {
    aspect-ratio: 816 / 464;
    max-width: 700px;
    height: auto;
  }
}

@media screen and (width >= 320px) and (width <= 767px) {
  .header {
    margin-bottom: 60px;
    padding: 25px 30px 0;
  }

  .header__list {
    position: static;
  }

  .header__inner-list {
    z-index: 20;
    grid-template-columns: min-content auto;
    grid-template-areas: "specs language"
                         "buy buy";
    gap: 162px 20px;
    width: calc(100vw - 60px);
    height: 0;
    transition: height .3s;
    display: grid;
    position: absolute;
    top: 300px;
    left: 30px;
    overflow: hidden;
  }

  .header__inner-list--active {
    height: auto;
  }

  .header__item:nth-of-type(3) {
    text-align: center;
    grid-area: buy;
    justify-content: center;
    align-items: center;
    width: 100%;
    display: flex;
  }

  .header__burger-menu:last-of-type {
    display: flex;
  }

  .specs {
    grid-area: specs;
    display: block;
  }

  .language {
    grid-area: language;
    display: block;
  }
}

