.article {
  grid-template-rows: auto 1fr;
  grid-template-columns: repeat(2, 1fr);
  gap: 30px;
  width: auto;
  display: grid;
}

.article__info {
  width: 100%;
}

.article__title {
  text-transform: uppercase;
  grid-area: title;
  font-size: 56px;
  font-weight: 700;
  line-height: 70px;
}

.article__text {
  color: #7c7c7c;
  margin-bottom: 60px;
